body, h1 {
  margin: 0;
}

.breed-select {
  color: #6e1515;
  background-color: #f0ebeb;
  border-width: 1px;
  border-color: rgba(145, 98, 41, .6);
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 5px;
  display: block;
}

.cat-info {
  flex-direction: column;
  justify-content: space-between;
  padding-top: 20px;
  display: flex;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.error {
  color: red;
}

.img-cat {
  object-fit: cover;
  width: 420px;
  margin-right: 25px;
  display: block;
}

h1 {
  color: #6e1515;
  size: 40px;
  margin-bottom: 20px;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

.cat-card {
  background-color: #f9f4e5;
  justify-content: center;
  padding: 20px;
  display: flex;
}

.cat-temperament {
  color: #916229;
  margin-top: auto;
  font-style: italic;
}

